import Vue from 'vue'
import Router from 'vue-router'
import Languages from '@/components/Languages'
import Chapters from '@/components/Chapters'
import Verse from '@/components/Verse/Verse'
import Login from '@/components/Login'
import About from '@/components/About'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Verse
    },
    {
      path: '/Languages',
      name: 'Languages',
      component: Languages
    },
    {
      path: '/Chapters',
      name: 'Chapters',
      component: Chapters
    },
    {
      path: '/Verse',
      name: 'Verse',
      component: Verse
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/About',
      name: 'About',
      component: About
    }
  ]
})



