import {HTTP} from '@/common/http-common'

export default {
  state: {
    scriptureId: 1,
    lang: null,
    verses: null,
    verseIndex: null,
    verseIndexBackup: null,
    languages: null,
    chapters: null,
    deferredPrompt: null,
    verseOfDayId: null,
    verseImage: null,
    isLoadingVerses: false
  },

  getters: {
    scriptureId (state) {
      if (state.scriptureId == null) {
        state.scriptureId = localStorage.getItem('scriptureId')
      }
      return state.scriptureId
    },

    lang (state) {
      if (state.lang == null) {
        state.lang = localStorage.getItem('lang')
      }
      return state.lang
    },

    languages (state) {
      if (state.languages == null) {
        state.languages = JSON.parse(localStorage.getItem('languages'))
      }
      return state.languages
    },

    chapters (state) {
      if (state.chapters == null) {
        state.chapters = JSON.parse(localStorage.getItem('chapters'))
      }
      return state.chapters
    },

    verses (state) {
      if (state.verses == null) {
        state.verses = JSON.parse(localStorage.getItem('verses'))
      }
      return state.verses
    },

    verseIndex (state) {
      if (state.verseIndex == null) {
        state.verseIndex = localStorage.getItem('verseIndex')
        if (state.verseIndex == null) {
          state.verseIndex = 0
          localStorage.setItem('verseIndex', 0)
        }
      }
      return state.verseIndex
    },

    verseIndexBackup (state) {
      if (state.verseIndexBackup == null) {
        state.verseIndexBackup = localStorage.getItem('verseIndexBackup')
        if (state.verseIndexBackup == null) {
          state.verseIndexBackup = 0
        }
      }
      return state.verseIndexBackup
    },

    verseByIndex (state, getters) {
      if (getters.verses) {
        return getters.verses[getters.verseIndex]
      } else {
        return null
      }
    },

    currentVerseId (state, getters) {
      return getters.verseByIndex.id
    },

    verseText (state, getters) {
      if (getters.verseByIndex) {
        return getters.verseByIndex.content[getters.lang].title.replace(/\n/g, '<br />')
      }
    },

    sanskritVerseText (state, getters) {
      if (getters.verseByIndex) {
        return getters.verseByIndex.content.sa.title.replace(/\n/g, '<br />')
      }
    },

    verseOfDayId (state) {
      if (state.verseOfDayId == null) {
        state.verseOfDayId = localStorage.getItem('verseOfDayId')
      }
      return Number(state.verseOfDayId)
    },

    isLoadingVerses (state) {
      return state.isLoadingVerses
    }
  },

  mutations: {
    verses (state, payload) {
      state.verses = payload
      localStorage.setItem('verses', JSON.stringify(state.verses))
    },

    lang (state, payload) {
      state.lang = payload
      localStorage.setItem('lang', state.lang)
    },

    languages (state, payload) {
      state.languages = payload
      localStorage.setItem('languages', JSON.stringify(state.languages))
    },

    chapters (state, payload) {
      state.chapters = payload
      localStorage.setItem('chapters', JSON.stringify(state.chapters))
    },

    incrementVerseIndex (state) {
      if (state.verseIndex !== 700) {
        state.verseIndex = parseInt(state.verseIndex) + 1
        localStorage.setItem('verseIndex', state.verseIndex)
      }
    },

    decrementVerseIndex (state) {
      if (state.verseIndex !== 0) {
        state.verseIndex = parseInt(state.verseIndex) - 1
        localStorage.setItem('verseIndex', state.verseIndex)
      }
    },

    verseIndex (state, payload) {
      state.verseIndex = payload
      localStorage.setItem('verseIndex', state.verseIndex)
    },

    verseIndexBackup (state, payload) {
      state.verseIndexBackup = payload
      localStorage.setItem('verseIndexBackup', state.verseIndexBackup)
    },

    verseOfDayId (state, payload) {
      state.verseOfDayId = payload
      localStorage.setItem('verseOfDayId', state.verseOfDayId)
    },

    resetState (state) {
      state.scriptureId = 1
      state.lang = null
      state.verses = null
      state.verseIndex = null
      state.isLoading = false
      state.error = null

      // clearing local storage
      localStorage.clear()

      // clearing cache
      caches.keys().then(function (names) {
        for (let name of names) {
          caches.delete(name)
        }
      })
    },

    toggleVerseLike (state, payload) {
      console.log('in toggle verse like mutation')
      for (var i = 0; i < state.verses.length; i++) {
        if (state.verses[i].id === payload) {
          state.verses[i].isLike = !state.verses[i].isLike
          break
        }
      }
    },

    isLoadingVerses (state, payload) {
      state.isLoadingVerses = payload
    }
  },

  actions: {
    fetchVerses ({commit, getters}, payload) {
      commit('isLoading', true)
      commit('isLoadingVerses', true)
      commit('lang', payload.lang)
      HTTP.get('verses', {params: {scriptureId: getters.scriptureId, lang: getters.lang}}).then(response => {
        commit('verses', response.data)
        commit('isLoading', false)
        commit('isLoadingVerses', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
        commit('isLoadingVerses', false)
      })
    },

    fetchLanguages ({commit, getters}, payload) {
      commit('isLoading', true)
      HTTP.get('languages').then(response => {
        commit('languages', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    fetchChapters ({commit, getters}, payload) {
      commit('isLoading', true)
      HTTP.get('chapters', {params: { scriptureId: getters.scriptureId, lang: getters.lang }}).then(response => {
        commit('chapters', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    incrementVerseIndex ({commit}) {
      commit('incrementVerseIndex')
    },
    decrementVerseIndex ({commit}) {
      commit('decrementVerseIndex')
    },
    verseIndex ({commit}, payload) {
      commit('verseIndex', payload)
    },

    changeChapter ({commit, getters}, payload) {
      var length = getters.verses.length
      for (var i = 0; i < length; i++) {
        var v = getters.verses[i]
        if (v.chapterId === payload.chapterId) {
          commit('verseIndex', i)
          break
        }
      }
    },

    resetState ({commit}) {
      commit('resetState')
    },

    changeIndexToVerseOfDay ({commit, getters}, payload) {
      var verseOfDayId = Number(payload.id)
      commit('verseOfDayId', verseOfDayId)
      var newIndex = 0
      for (var i = 0; i < getters.verses.length; i++) {
        if (getters.verses[i].id === verseOfDayId) {
          newIndex = i
          break
        }
      }
      commit('verseIndexBackup', getters.verseIndex)
      commit('verseIndex', newIndex)
    },

    resumeReading ({commit, getters}) {
      commit('verseOfDayId', null)
      commit('verseIndex', getters.verseIndexBackup)
    },

    toggleVerseLike ({commit, getters}, payload) {
      var endpoint = 'verses/' + getters.currentVerseId + '/likes'
      if (payload.isLike === true) {
        HTTP.delete(endpoint)
      } else {
        HTTP.post(endpoint)
      }
      commit('toggleVerseLike', getters.currentVerseId)
    }
  }
}



