import Vue from 'vue'
import Vuex from 'vuex'

import verse from './verse'
import user from './user'
import shared from './shared'
import verseComments from './verseComments'

Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    verse: verse,
    user: user,
    shared: shared,
    verseComments: verseComments
  }
})



