<template>
  <div id="app">
    <v-app>
   <v-navigation-drawer
      temporary
      v-model="drawer"
      absolute>
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <div v-if="userImage">
              <img :src="userImage" style="width:40px">
            </div>
            <div v-if="!userImage">
              <img src="https://www.vccircle.com/wp-content/uploads/2017/03/default-profile.png" style="width:40px">
            </div>
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>{{isLoggedIn ? user.name : 'Guest'}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
      <v-list class="pt-0" dense>
        <v-divider></v-divider>
        <v-list-tile v-if="!isLoggedIn" @click="login">
          <v-list-tile-action>
            <v-icon>face</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Signin</v-list-tile-content>
        </v-list-tile>
        <v-divider v-if="!isLoggedIn"></v-divider>

        <v-list-tile v-for="item in items" :key="item.title" :to="item.link">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-divider></v-divider>
        <v-list-tile v-if="isLoggedIn" @click="logout">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Logout</v-list-tile-content>
        </v-list-tile>

        <v-list-tile @click="reset">
          <v-list-tile-action>
            <v-icon>refresh</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Reset/Repair</v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

  <v-toolbar app height="45px">
    <v-toolbar-side-icon @click="drawer = !drawer"></v-toolbar-side-icon>
    Easyvedaz
  </v-toolbar>
  <v-content>
    <router-view></router-view>
  </v-content>
</v-app>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      drawer: null,
      items: [
        { title: 'Home', icon: 'dashboard', link: '/' },
        { title: 'Change Aadyay', icon: 'list', link: '/chapters' },
        { title: 'Change Language', icon: 'build', link: '/languages' },
        { title: 'Contact us', icon: 'question_answer', link: '/about' }
      ]
    }
  },
  computed: {
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    },

    user () {
      return this.$store.getters.user.userInfo
    },

    userImage () {
      var defaultImage = 'static/images/default-profile.png'
      return this.isLoggedIn ? this.user.image : defaultImage
    }
  },

  methods: {
    reset () {
      this.$store.dispatch('resetState')
      this.$router.push({
        path: '/'
      })
      window.location.reload(true)
    },
    logout () {
      this.$store.dispatch('logout')
      this.$router.push({
        path: '/'
      })
      window.location.reload(true)
    },
    login () {
      this.$router.push({
        path: '/login'
      })
    }
  }
}
</script>



