import {HTTP} from '@/common/http-common'

export default {
  state: {
    verseComments: []
  },

  getters: {
    verseComments (state) {
      return state.verseComments
    }
  },

  mutations: {
    verseComments (state, payload) {
      state.verseComments = payload
    },
    pushNewVerseComment (state, payload) {
      // console.log('in push', payload)
      state.verseComments.unshift(payload)
    },
    removeVerseComment (state, payload) {
      for (var i = 0; i < state.verseComments.length; i++) {
        if (state.verseComments[i].id === payload) {
          state.verseComments.splice(i, 1)
          break
        }
      }
    },
    toggleVerseCommentLike (state, payload) {
      for (var i = 0; i < state.verseComments.length; i++) {
        if (state.verseComments[i].id === payload) {
          state.verseComments[i].isLike = !+state.verseComments[i].isLike
          break
        }
      }
    }
  },

  actions: {
    fetchVerseComments ({commit, getters}, payload) {
      console.log('online', navigator.onLine)
      if (!navigator.onLine) {
        return
      }
      commit('isLoading', true)
      HTTP.get('verses/' + getters.currentVerseId + '/comments', {params: {lang: getters.lang}}).then(response => {
        // console.log('in fetch verse comments', response.data)
        commit('verseComments', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    saveVerseComment ({commit, getters}, payload) {
      return new Promise(function (resolve, reject) {
        commit('isLoading', true)
        var reqObj = {
          verseId: getters.currentVerseId,
          commentText: payload.commentText,
          lang: getters.lang
        }
        var endpoint = 'verses/' + getters.currentVerseId + '/comments'
        HTTP.post(endpoint, reqObj).then(response => {
          commit('pushNewVerseComment', response.data)
          commit('isLoading', false)
          resolve()
        }).catch(e => {
          commit('error', e)
          commit('isLoading', false)
          reject(e)
        })
      })
    },

    deleteVerseComment ({commit, getters}, payload) {
      commit('isLoading', true)
      var endpoint = 'verses/' + getters.currentVerseId + '/comments/' + payload
      HTTP.delete(endpoint).then(response => {
        commit('removeVerseComment', payload)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    toggleVerseCommentLike ({commit, getters}, payload) {
      var endpoint = 'verses/' + getters.currentVerseId + '/comments/' + payload.commentId + '/likes'
      if (payload.isLike === true) {
        HTTP.delete(endpoint)
      } else {
        HTTP.post(endpoint)
      }
      commit('toggleVerseCommentLike', payload.commentId)
    }
  }
}



