<template>
<div v-on:keyup.left="previousVerse" v-on:keyup.right="nextVerse" tabindex=-1>
  <v-container fluid class="mycontainer">
    <v-layout row wrap v-if="!isLoadingVerses">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
          <v-slide-x-transition>
            <v-card v-show="show">
                <v-card-title>
                    <div>
                        <span v-if="isVerseOfDay" >||Shloka of the day||<br></span>
                        <span class="primaryText" v-html="verseText"></span> <br><br>
                        <span class="secondaryText" v-html="sanskritVerseText"></span>
                    </div>
                </v-card-title>
                <v-card-actions>
                  <v-layout>
                    <v-flex xs4>
                      <v-btn flat @click="previousVerse()"><v-icon>arrow_back</v-icon>Back</v-btn>
                    </v-flex>
                    <v-flex xs4>
                      <v-btn icon @click="toggleVerseLike(currentVerseId, isLike)">
                        <v-icon v-if="isLike" color="pink">favorite</v-icon>
                        <v-icon v-else disabled>favorite</v-icon>
                      </v-btn>
                    </v-flex>
                    <v-flex xs4>
                      <v-btn v-if="isVerseOfDay" flat @click="resumeReading()"><v-icon>settings_backup_restore</v-icon>Resume</v-btn><br/>
                    </v-flex>
                  </v-layout>
                </v-card-actions>
            </v-card>
          </v-slide-x-transition>
        </v-flex>
        <v-flex>
          <v-btn class="fixedButton" color="error" dark medium fab @click="nextVerse()">
                <v-icon>arrow_forward</v-icon>
            </v-btn>
        </v-flex>
    </v-layout>

    <v-layout row wrap v-if="isLoading">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
            <br><br>
            <v-progress-circular indeterminate :size="100" color="amber"></v-progress-circular>
        </v-flex>
    </v-layout>

    <v-layout row>
      <v-flex xs12>
        <div style="padding-bottom:100px; padding-top:20px">
          <div v-for="comment in verseComments" :key="comment.id" style="padding:10px">
            <v-layout>
              <v-flex xs2>
                <v-avatar><img :src="comment.owner.image" alt=""/></v-avatar>
              </v-flex>
              <v-flex xs10>
                <h4>{{comment.owner.name}}</h4>
                <div>{{comment.commentText}}</div>
              </v-flex>
            </v-layout>
            <v-layout>
              <v-flex offset-xs8 xs2>
                <v-btn icon @click="toggleVerseCommentLike(comment.id, comment.isLike)">
                  <v-icon v-if="comment.isLike" color="red">thumb_up</v-icon>
                  <v-icon v-else color="grey lighten-1">thumb_up</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs2>
                <v-btn v-if="comment.owner.id === loggedinUserId" icon @click="deleteVerseCommentAlert(comment.id)"><v-icon color="grey lighten-1">delete</v-icon></v-btn>
              </v-flex>
            </v-layout>
            <hr>
          </div>
        </div>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="submit" >
          <div class="commentTextbox">
            <v-text-field flat multi-line rows="1" single-line counter=300 auto-grow label="Comment" v-model="commentText"
            append-icon="send" :append-icon-cb="submitVerseComment"></v-text-field>
          </div>
        </form>
      </v-flex>
    </v-layout>

    <template>
      <v-layout row justify-center>
        <v-dialog v-model="dialog" persistent max-width="290">
          <v-card>
            <v-card-title class="headline">Delete</v-card-title>
            <v-card-text>Do you want to delete the comment?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" flat @click.native="dialog = false">No</v-btn>
              <v-btn color="green darken-1" flat @click.native="deleteVerseComment()">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </v-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: true,
      commentText: null,
      dialog: false,
      commentIdToDelete: 0
    }
  },

  mounted () {
    var verseOfDayId = this.$route.query.id
    if (verseOfDayId) {
      this.$store.dispatch('changeIndexToVerseOfDay', {id: verseOfDayId})
    }
    this.$store.dispatch('fetchVerseComments')
  },

  computed: {
    currentVerseId () {
      return this.$store.getters.currentVerseId
    },
    isLike () {
      return this.$store.getters.verseByIndex.isLike
    },
    verseText () {
      return this.$store.getters.verseText
    },
    sanskritVerseText () {
      return this.$store.getters.sanskritVerseText
    },
    isLoading () {
      return this.$store.getters.isLoading
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    },
    isLoadingVerses () {
      return this.$store.getters.isLoadingVerses
    },
    verseOfDayId () {
      return this.$store.getters.verseOfDayId
    },

    isVerseOfDay () {
      return this.verseOfDayId === this.currentVerseId
    },

    verseComments () {
      return this.$store.getters.verseComments
    },

    loggedinUserId () {
      var userId = this.$store.getters.user ? Number(this.$store.getters.user.userInfo.id) : 0
      // console.log('in loggedinuserid', userId)
      return userId
    }
  },

  methods: {
    nextVerse () {
      this.$store.dispatch('incrementVerseIndex')
      this.$router.replace({query: {id: this.currentVerseId}})
      this.$store.dispatch('fetchVerseComments')
    },
    previousVerse () {
      this.$store.dispatch('decrementVerseIndex')
      this.$router.replace({query: {id: this.currentVerseId}})
      this.$store.dispatch('fetchVerseComments')
    },
    resumeReading () {
      this.$store.dispatch('resumeReading')
      this.$router.replace({query: {id: this.currentVerseId}})
      this.$store.dispatch('fetchVerseComments')
    },
    submitVerseComment () {
      console.log('in submit comment')
      this.$store.dispatch('saveVerseComment', {commentText: this.commentText}).then(r => {
        this.commentText = null
      })
    },
    deleteVerseCommentAlert (id) {
      this.commentIdToDelete = id
      this.dialog = true
    },
    deleteVerseComment () {
      this.dialog = false
      this.$store.dispatch('deleteVerseComment', this.commentIdToDelete)
    },
    toggleVerseLike (id, isLike) {
      console.log('in toggleverselike', id)
      this.$store.dispatch('toggleVerseLike', {verseId: id, isLike: isLike})
    },
    toggleVerseCommentLike (id, isLike) {
      this.$store.dispatch('toggleVerseCommentLike', {commentId: id, isLike: isLike})
    }
  }
}
</script>
<style scoped>
    .fixedButton {
        position: fixed;
        opacity: 0.4;
        bottom: 30%;
        right: 20px;
        z-index:100;
    }
    .commentTextbox {
      position:fixed;
      bottom:0%;
      width:95%;
      z-index:50;
      background-color:white;
    }
    .primaryText {
        font-family: 'Arya', sans-serif;
        font-size: 20px;
        color: #212121;
    }
    .secondaryText {
        font-family: 'Karma', serif;
        font-size: 20px;
        color: #757575;
    }

@media only screen and (max-width: 599px) {
    .mycontainer {
      padding: 5px !important;
    }
}
</style>



