import {HTTP} from '@/common/http-common'

export default {
  state: {
    user: null,
    authNetwork: null
  },

  getters: {
    user (state) {
      if (state.user == null) {
        state.user = JSON.parse(localStorage.getItem('user'))
      }
      return state.user
    },

    authNetwork (state) {
      if (state.authNetwork == null) {
        state.authNetwork = localStorage.getItem('authNetwork')
      }
      return state.authNetwork
    },

    isLoggedIn (state, getters) {
      return getters.user != null
    },

    token (state, getters) {
      return getters.user ? getters.user.accessToken : null
    }
  },

  mutations: {
    user (state, payload) {
      state.user = payload
      localStorage.setItem('user', JSON.stringify(state.user))
    },
    logout (state) {
      localStorage.removeItem('user')
      localStorage.removeItem('authNetwork')
    },
    authNetwork (state, payload) {
      state.authNetwork = payload
      localStorage.setItem('authNetwork', state.authNetwork)
    }
  },

  actions: {
    authenticate ({commit, getters}, payload) {
      return new Promise(function (resolve, reject) {
        var networkName = getters.authNetwork
        var networkCode = networkName === 'google' ? 1 : 2
        var reqObj = {
          thirdPartyAccessToken: payload,
          GrantType: 'implicit',
          notificationPlayerId: payload.notificationPlayerId,
          tokenProvider: networkCode
        }

        commit('isLoading', true)
        HTTP.post('token', reqObj).then(response => {
          commit('user', response.data)
          commit('isLoading', false)
          resolve()
        }).catch(e => {
          commit('error', e)
          commit('isLoading', false)
          reject(e)
        })
      })
    },

    logout ({commit}) {
      commit('logout', null)
    },

    authNetwork ({commit}, payload) {
      commit('authNetwork', payload)
    },

    submitPlayerId ({commit, getters}, payload) {
      var reqObj = {
        notificationPlayerId: payload
      }

      HTTP.post('pushnotification/subscriptions', reqObj).then(response => {
      }).catch(e => {
        commit('error', e)
        console.log(e)
      })
    }
  }
}



