<template>
  <div>
    <v-container class="mycontainer">
      <v-layout row wrap>
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
          <br><br>Login/Register using Google/Gmail Account<br><br>
          <div id="google-signin-btn"></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  mounted () {
    window.gapi.signin2.render('google-signin-btn', {
      onsuccess: this.onSignIn
    })
  },
  methods: {
    onSignIn (googleUser) {
      this.$store.dispatch('authNetwork', 'google')
      var idToken = googleUser.getAuthResponse().id_token

      var vm = this
      this.$store.dispatch('authenticate', idToken).then(function () {
        vm.$router.go(-1)

        if (window.OneSignal) {
          window.OneSignal.isPushNotificationsEnabled().then(function (isEnabled) {
            if (!isEnabled) {
              window.OneSignal.push(() => {
                window.OneSignal.registerForPushNotifications({
                  modalPrompt: true
                })
                window.OneSignal.setSubscription(true)
                window.OneSignal.getUserId().then(function (userId) {
                  vm.$store.dispatch('submitPlayerId', userId)
                })
              })
            } else {
              window.OneSignal.getUserId().then(function (userId) {
                vm.$store.dispatch('submitPlayerId', userId)
                vm.$router.go(-1)
              })
            }
          })
        }
      })
    }
  }
}
</script>



