export default {
  state: {
    isLoading: false,
    error: null
  },

  getters: {
    isLoading (state) {
      return state.isLoading
    },

    error (state) {
      return state.error
    }
  },

  mutations: {
    isLoading (state, payload) {
      state.isLoading = payload
    },

    error (state, payload) {
      state.error = payload
    }
  },

  actions: {
    isLoading ({commit}, payload) {
      commit('isLoading', payload)
    },

    error ({commit}, payload) {
      commit('error', payload)
    }
  }
}



